.sm {
    --size: var(--sm);
}

.md {
    --size: var(--md);
}

.lg {
    --size: var(--lg);
}

.card-deck {
    margin: 5vh auto;
}

.card-deck.gallery {
    position: relative;
}

.card-deck.gallery .card:hover {
    z-index: 1;
}

.card-deck.gallery .card {
    perspective-origin: 50% 50%;
    perspective: 1000px;
}

.card-deck.gallery .card {
    position: absolute;
    transition-property: width, left, top, opacity;
    transition-duration: 200ms;
    transition-timing-function: ease-out;
    perspective-origin: 50% 50%;
    perspective: 1000px;
    left: var(--left);
    top: var(--top);
    width: var(--width);
}

.gallery.card {
    transform: translate(0, 0) rotateX(0) rotateY(0) !important;
}

.card-body {
    transition-property: transform, width, left, top, opacity,padding-bottom;
    transition-duration: 200ms;
    transition-timing-function: ease-out;
    transform-style: preserve-3d;

    position: relative;
    aspect-ratio: 1;
    border-radius: 5px;
    background-size: 100%, 100%;
    overflow: hidden;
    user-select: none;
    box-shadow: 5px 5px 10px #010101aa;
    z-index: 2;
}

.card-deck.gallery .card-body {
    cursor: pointer;
}

.card-deck.gallery .card.hidden .card-body {
    cursor: default;
    opacity: 0;
    transform: rotateX(-60deg) rotateY(10deg) !important;
}

.card-deck.gallery .card.focus {
    z-index: 3;
}

.card-deck.gallery .card.focus .card-body {    
    transition-duration: 0ms;
}

.card-deck.gallery .gallery.card .card-body:hover  {
    transform: translateZ(calc(100px / var(--size))) rotateX(calc(20deg / var(--size))) !important;
}

.card-overlay {
    width: 100%;
    aspect-ratio: 1;
    mix-blend-mode: lighten;
    transition: background-position 200ms ease-out;
    background-image: linear-gradient(10deg, #ffffff00 0%, #ffffff0a 46%, #ffffff1a 65%, #ffffff2c 72%, #ffffff11 100%);
    background-size: 200% 200%;
    background-position: 50% 70%;
}

.card-deck.gallery .gallery.card:hover .card-overlay {
    background-position: 50% calc(70% - 100% / var(--size)) !important;
}

.focus.card .card-overlay {
    transition: none;
}

.card-header {
    position: absolute;
    padding: .4em .6em;
    margin: 0;
    bottom: 0; 
    left: 0;
    border-radius: 0 5px 0 0;
    background-color: rgba(0, 0, 0, .6);
    transition: opacity 200ms ease-out, font-size 200ms ease-out;
}

.card .card-header {
    opacity: 0;
}

.gallery.card:hover .card-header {
    opacity: 1;
}

.focus.card .card-header {
    opacity: 1;
    font-size: calc(var(--deckwidth) * .07);
}

.card .card-header {
    font-size: calc(.014 * var(--deckwidth) * var(--size));
}

.card-link {
    text-align: center;
    width: 100%;
    letter-spacing: .2em;
}

.card-desc {
    position: absolute;
    padding: 1em;
    z-index: 0;
    transform: translate(0, -100%);
    max-height: 90%;
    width: 100%;
    box-sizing: border-box;
    /* overflow: hidden; */
}

.card-deck .card-desc {
    transition: transform 400ms ease-out;
    transition-delay: 300ms;
    visibility: hidden;
}

.card-deck.mobile .card .card-body {
    transition: transform 200ms ease-out;
}

.card-deck.mobile .card.focus .card-body {
    transform: scale(1.07);
}

.card-deck.mobile .card {
    transition: padding 400ms ease-out;
    position: static;
    width: 250px;
}

.card-deck.mobile .card-desc {
    background-color: #080808dd;
    border-radius: 5px;
    transition: transform 400ms ease-out;
    width: 250px;
}

.card-deck.mobile .card.focus {
    padding-bottom: 100%;
}

.card-desc p {
    transition: font-size 100ms ease-out;
}

.card-desc p:first-child {
    margin-top: 0 !important;
    font-weight: 100;
}

.card-desc p:last-child {
    margin-bottom: 0 !important;
}

.focus.card .card-desc {
    visibility: visible;
    transform: translate(0, 0);
    font-size: 2.1vh;
}