:root {
    --card-spacing: 1vh;
    --sm: 2;
    --md: 3;
    --lg: 4;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
::-webkit-scrollbar-track {
    border-radius: 10px;
}
::-webkit-scrollbar-thumb{
    border-radius: 10px;
    background: #444;
}
::-webkit-scrollbar-thumb:hover{
    background: #666; 
}
::-webkit-scrollbar-thumb:active{
    background: #888;
}

body {
    margin: 0;
    background-color: #121212;
}

.section-header {
    margin: 0 auto;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-size: 6vh;
    width: max-content;
    /* margin-bottom: 0; */
}

.header-underline {
    height: 6px;
    animation:  background-color-cycle 10s linear 0s infinite normal none,
                shadow-color-cycle 10s linear 0s infinite normal none;
}

.text-header {
    margin-bottom: 0;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

.text-body {
    color:white;
    font-family: 'Roboto', sans-serif;
}

.no-link {
    cursor: default;
    text-decoration: none !important;
}

a, .no-link {
    text-decoration: none;
    animation: text-color-cycle 10s linear 0s infinite normal none;
}

a:hover {
    text-decoration: underline;
}

#bg-anim {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: -1;
}

@keyframes background-color-cycle {
    0%   {background-color: #fcc;}
    16%  {background-color: #ffc;}
    33%  {background-color: #cfc;}
    50%  {background-color: #cff;}
    66%  {background-color: #ccf;}
    87%  {background-color: #fcf;}
    100% {background-color: #fcc;}
}

@keyframes shadow-color-cycle {
    0%   {box-shadow: 0 0 20px #f00;}
    16%  {box-shadow: 0 0 25px #ff0;}
    33%  {box-shadow: 0 0 20px #0f0;}
    50%  {box-shadow: 0 0 25px #0ff;}
    66%  {box-shadow: 0 0 20px #00f;}
    87%  {box-shadow: 0 0 25px #f0f;}
    100% {box-shadow: 0 0 20px #f00;}
}

@keyframes text-color-cycle {
    0%   {color: #faa;}
    16%  {color: #ffa;}
    33%  {color: #afa;}
    50%  {color: #aff;}
    66%  {color: #aaf;}
    87%  {color: #faf;}
    100% {color: #faa;}
}

/* mobile */
@media all and (max-width: 767.9px) {
    .content-container {
        width: 100%;
    }

    .card-deck {
        --deckwidth: 250px;
        width: 250px;
    }

    .card {
        margin-top: 30px;
    }

    .card-header {
        opacity: 1 !important;
        font-size: 20px !important;
    }

    .section-header {
        font-size: 30px;
    }

    .scale-text-body {
        font-size: 14px !important;
    }

    .splash-container {
        height: 100vh;
    }

    .splash-text-container {
        top: 40%;
    }

    .splash-text-container h1 {
        font-size: 10vw;
    }

    .splash-text-container h2 {
        font-size: 4.5vw;
    }
}

/* larger screens */
@media all and (min-width: 768px) {
    .content-container {
        width: 80%;
    }

    .card-deck {
        --deckwidth: 50vh;
        width: 50vh;
    }
}